<template>
  <table class="table-list-main">
    <div class="hidden-init-column">
      <slot></slot>
    </div>
    <table-thead
      :store="store">
    </table-thead>
    <table-tbody
      :store="store">
    </table-tbody>
  </table>
</template>




<script>
import TableThead from './TableThead';
import TableTbody from './TableTbody';
import Store from './store/store.js';

export default {
  name: "ListTable",
  components: {
    TableThead,
    TableTbody
  },
  props: {
    dataSource: {
      type: Array,
      required: true
    },
    isClearCheckbox: {
      type: Boolean,
      required: true
    }
  },
  computed: {
    seletions() {
      return this.store.states.seletions;
    }
  },

  watch: {
    dataSource: {
      immediate: true,
      deep: true,
      handler(value) {
        this.store.states.data = value;
      }
    },

    seletions: {
      immediate: false,
      deep: true,
      handler(value) {
        if (value.length < 1) {
          this.store.states.checkboxStates = 'normal'
        }
        this.$emit('getCheckboxSeletions', value)
      }
    },

    isClearCheckbox: {
      immediate: true,
      handler(newval) {
        if ( newval ) {
          this.store.states.checkboxStates = 'noChecked';
          newval = !newval;
        }
      }
    }
  },

  data() {
    let store = new Store(this);
    return {
      store,
    }
  },
  methods: {
    /* 实现对外提供checkbox的选择 */
