    //   }
    //   if(option == 'edit') {
    //     if (seletions.length > 1 || seletions.length <= 0) {
    //       return ''
    //     }else {
    //       let id = seletions[0];
    //       this.store.states.data.forEach(dataObj => {
    //         if(dataObj.id == id) {
    //           resultObj = deepCloneObject(dataObj);
    //         }
    //       })
    //       return resultObj
    //     }
    //   }
    // },

    /* 实现对外清空checkbox的选择 */
    // clearCheckbox() {
    //   this.store.states.checkboxStates = 'noChecked';
    // }
  },
}
</script>

